@charset "utf-8";
@import "bulma.sass";
@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');

.image.avatar {
    height: 256px;
    width: 256px; }

.hero-foot img {
    display: block;
    margin: 0 auto; }

